// mqqwpa://im/chat?chat_type=wpa&uin=123456789&version=1&src_type=web&web_src=oicqzone.com
  import {QQ} from '../config'
  export default {
    name: 'orderItem',
    props: {
      list: {
        type: Array,
        default: []
      }
    },
    data () {
      return {
        userId: '',
        qq: QQ
      }
    },
    created () {
      this.userId = this.$route.params.userId
    },
    methods: {
      changeStatus (num) {
        switch (num) {
          case -1:
            return '已撤单'
          case 0:
            return '待保险公司报价'
          case 1:
            return '核保中'
          case 2:
            return '核保失败'
          case 3:
            return '待付款'
          case 4:
            return '待承保'
          case 5:
            return '已承保'
        }
      },
      changePrice (item) {
        switch (item.orderStatus) {
          case -1:
            return '0.00'
          case 0:
            return '待报价'
          case 1:
            return '暂无保费'
          case 2:
            return '暂无保费'
          case 3:
            return '￥' + item.amount
          case 4:
            return '￥' + item.amount
          case 5:
            return '￥' + item.amount
        }
      },
      handleSaveData (item) {
        this.$router.push('/orderdetail/' + this.$route.params.userId + '/' + item.id)
      }
    }
  }



